import React, {useEffect,useState} from 'react';
import '../../style/App.css';
import {connect} from "react-redux";

import {withRouter} from "react-router";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import ListMessageCont from "./ListMessage/ListMessageCont";
import ListFriend from "./ListMan/ListFriend";
import MenuHeader from "../../Header/MenuHeader";
import LoAd from "../../conponent/load";
import Cookies from "js-cookie";
import {GetListFriend, GetListMessages, SendMessage} from "./MessageReducer";
import {getFriend, getMessage} from "../../user-selector/MessageSelector";

const MessageCont= React.memo(({
						 message
						,friend
						,state
						,GetListMessages
						,GetListFriend
						,SendMessage
						,match
})=>{

	const [user, setUSer] = useState(state.user.id)
	const [profile, setProfile] = useState(parseInt(match.params.profileID) || parseInt(state.user.id)  )

	const [messageID, setMessage] = useState(message)
	const [friendID, setFriend] = useState(friend)



	useEffect(() => {
		debugger
		GetListFriend()
		GetListMessages(profile);
		Cookies.set('History', 'message')
	},[]);
	useEffect(() => {
		debugger
		setMessage(message);
		setFriend(friend)
	},[friend,message]);


	const onSubmit = (formData) => {
		if(formData.message!=="" && formData.message!==" "){
		SendMessage(state.user.id, formData.message)
		document.getElementById('textarea').value = "";
		}
	};
	debugger
	return <div>
				{
					state.loading
					? <div>
						<LoAd/>
					</div>

					: <div>
						<div>
							<MenuHeader/>
						</div>

						<div className={'MESSAGE'}>
							<ListMessageCont
								user={user}
								profile={profile}
								message={messageID}

								onSubmit={onSubmit}
							/>
							<ListFriend
								friend={friendID}
								getListMessages={GetListMessages}
							/>
						</div>
					</div>
				}
	</div>

});
let mapStateToProps= (state) =>{
	return {
	message:state.MessageReducer.message
	,friend:state.MessageReducer.friend
	}
};

export default compose(
	connect(mapStateToProps,{GetListFriend,GetListMessages,SendMessage } )
	,withRouter
	,withAuthRedirect
)(MessageCont)




