import React from 'react'
import '../../../style/App.css';

let ListFriend=React.memo(props=>{
    let DialogItem = props.friend.map( (i)=> {
            return (
                <div className={"FRIEND_ITEM"}
                     onClick={()=> {
                         debugger
                         props.getListMessages(i.user);

                     }}
                >
                    <img className={"FRIEND_AVA"}
                         src={i.picture}
                         alt={"Pic"}
                    />
                    <a>
                        <div>{i.name} {i.lastname}</div>
                        <div>{i.status}</div>
                    </a>
                </div>
            )
        }
    );
    return  (  <div className={"FRIENDS_LIST"}>
                     {DialogItem}
               </div>
    )
});

export default ListFriend;



