import React from 'react'

let ListMessage=React.memo(props=>{
    debugger
       let messageItem = props.message.map(i => (<ItemM message={i.message} UserInMessages={i.user} UserGetingMessages={props.user}/>));
        return(
            <div className={"MESSAGE_LIST"}>
                        {messageItem}
            </div>
        )
});
export default ListMessage;

let ItemM=React.memo(props=>{
    return  <div>
                  {
                     props.UserInMessages===props.UserGetingMessages
                     ?<div className={"MESSAGE_ITEM"} id={'USER'} >
                             {props.message}
                     </div>
                     :<div className={"MESSAGE_ITEM"} id={'PROFILE'} >
                             {props.message}
                     </div>
                  }
            </div>
});
