import React from 'react'
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../../../conponent/FormsControll/FormsControll";

let FormMessage=React.memo(props=>{
    return (
        <div>
              <form onSubmit={props.handleSubmit} >
                    <div className={"MESSAGE_FORM"}>
                        <Field   component={Textarea}
                                 name={'message'}
                                 placeholder={'Messages'}
                                 id={'textarea'}
                                 className={'pole_M'}
                        />

                        <button className={'button_M'}>Enter</button>
                    </div>
              </form>
        </div>
          )
});

const ReduxFormMessage=reduxForm({form:'message'})(FormMessage)
export default ReduxFormMessage;



