import React from 'react'
import ListMessage from './ListMessage'
import ReduxFormMessage from './FormMessage'

let ListMessageCont =React.memo(props=> {
    debugger
    return (
        <div>
              <ListMessage message={props.message}

                           profile={props.profile}
                           user={props.user}
              />
              <ReduxFormMessage onSubmit={props.onSubmit}  />
        </div>
    )
});
export default ListMessageCont


